<template>
  <div
    class="horizontal-calendar"
    v-bind:style="{'border-top':showBorderTop?'1px solid #f2f2f2':'none'}"
  >
    <span class="left-arrow" v-on:click="dateFlip(-1)">
      <slot name="leftIcon" v-if="this.$slots['leftIcon']"></slot>
      <svg v-else class="icon" aria-hidden="true">
        <use xlink:href="#jac-left" />
      </svg>
    </span>
    <div class="date-list">
      <div
        class="date-list-scroll"
        :style="{'transform': 'translateX('+translateX+'px)','transition-duration': transitionDuration}"
      >
        <div
          class="date-item"
          v-for="(day,index) in dateList"
          v-bind:key="index"
          v-on:click="changeChoosedDay(day)"
          v-bind:class="{'choosed-day': day.dateFormat == choosedDay.dateFormat,'today':day.dateFormat == today.dateFormat && lang == 'zh'}"
          v-bind:style="{'background-color': day.dateFormat == choosedDay.dateFormat?choosedItemColor:day.dateFormat == today.dateFormat?todayItemColor:''}"
        >
          <div>
            <p class="date-item-day">{{day.day}}</p>
            <p
              class="date-item-date"
              v-if="day.dateFormat == today.dateFormat && lang == 'zh'"
              v-bind:style="{'color': day.dateFormat == choosedDay.dateFormat?'#fff':day.dateFormat == today.dateFormat?choosedItemColor:''}"
            >ä»</p>
            <p class="date-item-date" v-else>{{day.date}}</p>
          </div>
          <div class="first-day" v-if="day.date == 1">
            <p>{{day.month}}</p>
          </div>
        </div>
      </div>
    </div>
    <span class="right-arrow" v-on:click="dateFlip(1)">
      <slot name="rightIcon" v-if="this.$slots['rightIcon']"></slot>
      <svg v-else class="icon" aria-hidden="true">
        <use xlink:href="#jac-right" />
      </svg>
    </span>
  </div>
</template>
<script>
import "@/assets/iconfont.js";
export default {
  name: "vue-horizontal-calendar",
  props: {
    // é»è®¤éä¸­çæ¥æ, ä»å¤©ï¼ å¯æ¥æ¶æ ¼å¼å¦ â2019/12/01â æ â2019-12-01â æ åUTCæ ¼å¼æ¶é´
    choosedDate: {
      type: [String, Date],
      required: false,
      default: () => {
        return new Date();
      }
    },
    //ç¹å»å·¦å³ç®­å¤´ï¼åæ¢çæ¥æé´éå¤©æ°
    swipeSpace: {
      type: [String, Number],
      required: false,
      default: 7
    },
    // å½åé»è®¤éä¸­çæ¥ææå¤çä½ç½®ï¼'leftï¼center'ï¼é»è®¤å·¦
    choosedDatePos: {
      type: String,
      required: false,
      default: "left"
    },
    // æå°æ¥æï¼å¯æ¥æ¶æ ¼å¼å¦ â2019/12/01â æ â2019-12-01â æ æ åUTCæ ¼å¼æ¶é´
    minDate: {
      type: [String, Date],
      required: false,
      default: ""
    },
    // æå¤§æ¥æï¼å¯æ¥æ¶æ ¼å¼å¦ â2019/12/01â æ â2019-12-01â æ æ åUTCæ ¼å¼æ¶é´
    maxDate: {
      type: [String, Date],
      required: false,
      default: ""
    },
    // éä¸­çæ¥æèæ¯è²
    choosedItemColor: {
      type: String,
      required: false,
      default: "rgb(13, 141, 224)"
    },
    // âä»å¤©âæªéä¸­æ¶çèæ¯è²
    todayItemColor: {
      type: String,
      required: false,
      default: "rgba(13, 141, 224,.1)"
    },
    // ææå¤©çä¸­æå­ï¼é»è®¤âæ¥âï¼å¯èªå®ä¹ï¼å¦âå¤©â
    sundayText: {
      type: String,
      required: false,
      default: "æ¥"
    },
    // æ¯å¦æ¾ç¤ºæ¥åç»ä»¶çé¡¶é¨è¾¹æ¡
    showBorderTop: {
      type: Boolean,
      required: false,
      default: true
    },
    // å±å¹å°ºå¯¸æ¹åæ¶ï¼æ¯å¦éç»æ¥åç»ä»¶
    resizeable: {
      type: Boolean,
      required: false,
      default: true
    },
    // è¯­è¨ï¼å¯éå¼ï¼zh(ä¸­æ)ï¼en(è±æ)
    lang: {
      type: String,
      required: false,
      default: "zh"
    }
  },
  data() {
    return {
      // æ¯ä¸ä¸ªæ¥æå¯¹è±¡çæ°æ®ç»æå¦ä¸
      // {
      //   dateFormat: "2019/07/20",
      //   year: "2019",
      //   month: "07",
      //   date: "20",
      //   timestamp: 1564046915797,
      //   day: "å­"
      // }

      firstDay: {}, //å½åæ¾ç¤ºçç¬¬ä¸ä¸ªæ¥æ
      visibleDay: "", // è®¡ç®å±å¹å¯æ¾ç½®çæ¥ææ¹åçæ°é
      changeCount: Number(this.swipeSpace), //ç¹å»å·¦å³ç®­å¤´ï¼å¢éçæ¥ææ°éï¼é»è®¤ä¸º7å¤©
      choosedDay: {}, // å½åéä¸­çæ¥æ
      dateList: [], // å½åçæ¥æåè¡¨
      today: {}, // ä»å¤©

      // å¨ç»æ°æ®
      translateX: -this.changeCount * 50,
      transitionDuration: "300ms",
      domWidth: 0, // æ¥åç»ä»¶å®½åº¦

      swipeLeftMore: true, // æ¯å¦è¿è½å·¦æ»
      swipeRightMore: true // æ¯å¦è¿è½å³æ»
    };
  },
  mounted() {
    this.init();
    // è§¦åchangeäºä»¶
    this.$emit("change", this.choosedDay);

    /**
     * @@author jacques
     * @@date 2019.12.06
     * @@desc çå¬å±å¹å®½åº¦ååäºä»¶ï¼å¨ææ´æ°æ¥ææ¡
     * å ä¸ºonresizeäºä»¶å¨ä¸æ¬¡ææ½è¡ä¸ºä¸­ï¼ä¼è°ç¨nå¤æ¬¡ï¼ä¸ºäºä¸å¿è¦çæ°æ®æ´æ¿åçé¢æ¸²æï¼æ­¤å¤å®ä¹a,bä¸¤ä¸ªåéï¼éè¿å»¶æ¶å¤æ­ï¼æ¥æ¶é¤è¿ç§æ è°çæ¶è
     * */
    if (this.resizeable) {
      let a = 1;
      window.onresize = () => {
        a++;
        let b = a;
        setTimeout(() => {
          if (b == a) {
            this.init();
          }
        }, 200);
      };
    }
  },
  methods: {
    init() {
      // æ ¹æ®å±å¹å®½åº¦ï¼å¤æ­å¯æ¾ç¤ºçæ¥ææ¹åçæ°é
      this.domWidth = this.$el.scrollWidth;
      let n = (this.domWidth - 60) / 50;
      // å¯æ¾ç¤ºæ¥ææ°é = å¯å®å¨æ¾ç¤ºçæ°é+1 ï¼é¤édivçå®½åº¦ååå¥½è¢«50æ´é¤ï¼
      if (n % 1 != 0) {
        n = parseInt(n) + 1;
      }
      this.visibleDay = n;

      // "ä»å¤©"
      this.today = this.formatOneDay(new Date());
      // "å½åéä¸­çæ¥æ"
      this.choosedDay = this.formatOneDay(this.choosedDate);

      // ç¬¬ä¸å¤©
      let firstDay = this.formatOneDay(this.choosedDate);
      if (this.choosedDatePos === "center") {
        let ts =
          firstDay.timestamp -
          parseInt(this.visibleDay / 2) * 1000 * 60 * 60 * 24;
        this.firstDay = this.formatOneDay(ts);
      } else {
        this.firstDay = firstDay;
      }
      this.creatList();

      // äºä»¶åè°: å½åæ¾ç¤ºçç¬¬ä¸å¤©çæ°æ®
      this.$emit("firstDayChange", this.firstDay);
    },
    // åå§åï¼çæä¸åæ¥æ
    creatList() {
      let list = [];
      let firstDayTime = this.firstDay.timestamp;
      for (let i = 0; i < this.visibleDay; i++) {
        let timestamp = firstDayTime + 1000 * 60 * 60 * 24 * i; //ç¨ç¬¬ä¸å¤©çæ¶é´æ³+24å°æ¶*iå¤©
        let info = this.formatOneDay(new Date(timestamp));
        list.push(info);
      }
      this.dateList = list;
      this.translateX = 0;

      // éç½®å·¦å³æ»å¨éå¶
      this.swipeLeftMore = true;
      this.swipeRightMore = true;
    },
    // æ¥æç¹å»äºä»¶
    changeChoosedDay(day) {
      this.choosedDay = day;
      this.$emit("change", day);
    },
    // å·¦å³æ»å¨ç¿»é¡µ 1ï¼å¾åå è½½7å¤©ï¼-1ï¼å¾åå è½½7å¤©
    dateFlip(type) {
      // æ°æ®åæ´åçé¨åæ°æ®
      let beforeChangeX = this.translateX;
      let beforeChangeLen = this.dateList.length;

      // å·¦è¾¹æé®
      if (type === -1) {
        // 1ï¼å¦ææ­¤æ¶translateX< åæ¬¡æ»å¨çæ¥æé¿åº¦ï¼è¯´æå·¦ä¾§æå¯æ»å¨çæ¥æï¼ä¸éè¦çææ´å¤æ¥æ
        if (this.translateX <= -50 * this.changeCount) {
          this.translateX = this.translateX + 50 * this.changeCount;

          // 2ï¼å¦æç±äºæå°æ¥æéå¶ï¼å è½½å·²ç»å°å¤´ï¼åä¸åå è½½æ°çæ¥æ,ç´æ¥æ»å¨å°æå·¦è¾¹
        } else if (!this.swipeLeftMore) {
          this.translateX = 0;
        } else {
          //,3ï¼ä»¥ä¸æ¡ä»¶é½ä¸æ»¡è¶³ï¼åè¯´æå·¦ä¾§å·²ç»æ²¡æå¯å±ç¤ºçæ¥æäºï¼è¦æ°å¢æ°æ®
          let fdt = this.dateList[0].timestamp;
          let list = [];
          let i;
          for (i = 0; i < this.changeCount; i++) {
            //ç¨æ°ç»ç¬¬ä¸é¡¹çæ¶é´æ³ - 24å°æ¶*(i+1)å¤©
            let timestamp = fdt - 1000 * 60 * 60 * 24 * (i + 1);
            let info = this.formatOneDay(new Date(timestamp));
            // å¦æå­å¨æå°æ¥æéå¶,ä¸å½åè¿ä¸ªæ¥æå°äºæå°éå¶ï¼ååæ­¢å¾ªç¯
            if (this.minDate && info.timestamp < this.minDateTimestamp) {
              this.swipeLeftMore = false;
              break;
            } else {
              list.unshift(info);
            }
          }
          // æ¶é¤è¿æ¸¡ææï¼æå¥æ°æ¥æï¼åæ´xè½´ä½ç½®ï¼ä»¥æµæ¶æå¥æ°æ®å¸¦æ¥çä½ç½®åå
          this.transitionDuration = "0ms";
          this.dateList.unshift(...list);
          // i è¡¨ç¤ºæ°å¢çæ¥ææ°éï¼ å ä¸ºå¾ªç¯å¯è½ä¼è¢«æå¤§æå°å¼ä¸­æ­ï¼æä»¥ i ç¸å¯¹äº changeCount æ´åç¡®
          this.translateX = this.translateX - 50 * i;

          // å¼æ­¥éç½®è¿æ¸¡ææï¼ä½ç§»div
          setTimeout(() => {
            this.transitionDuration = "300ms";
            this.translateX = this.translateX + 50 * i;
          }, 1);
        }
        // å³è¾¹æé®
      } else if (type === 1) {
        // å¤æ­å³ä¾§æ¯å¦æå¯æ»å¨çæ¥æï¼æçè¯åç´æ¥æ»å¨
        let hasSpace =
          this.dateList.length * 50 - this.domWidth + this.translateX;

        // 1ï¼æå®æ´å¯æ»å¨çæ¥æï¼åç´æ¥æ»å¨
        if (hasSpace > 50 * this.changeCount) {
          this.translateX = this.translateX - 50 * this.changeCount;
        } else {
          // 2ï¼å¦æç±äºæå¤§æ¥æéå¶ï¼å è½½å·²ç»å°å¤´ï¼åä¸åå è½½æ°çæ¥æ; ç´æ¥æ»å¨å°æ«ç«¯ï¼
          if (!this.swipeRightMore) {
            this.translateX = (this.dateList.length - this.visibleDay) * -50;
            return;
          }
          // 3ï¼ä»¥ä¸é½ä¸æ»¡è¶³ï¼åéè¦çææ°æ¥æï¼ç¶åæ»å¨
          let fdt = this.dateList[this.dateList.length - 1].timestamp;
          let i;
          for (i = 0; i < this.changeCount; i++) {
            //ç¨æ°ç»ç¬¬ä¸å¤©çæ¶é´æ³ + 24å°æ¶*(i+1)å¤©
            let timestamp = fdt + 1000 * 60 * 60 * 24 * (i + 1);
            let info = this.formatOneDay(new Date(timestamp));
            // å¦æå­å¨æå¤§æ¥æéå¶,ä¸å½åæ¥æå¤§äºæå¤§éå¶ï¼ååæ­¢å¾ªç¯
            if (this.maxDate && info.timestamp > this.maxDateTimestamp) {
              this.swipeRightMore = false;
              break;
            } else {
              this.dateList.push(info);
            }
          }
          // å¦æiå°äºåºå®ç§»å¨åä½ï¼åè¯´æç±äºæå¤§æ¥æéå¶ï¼æåä½ç§»çåä½å¹¶ä¸å®å¨åè¶³ï¼æ­¤æ¶içå¼ï¼åæå°æ¥æåå±å¹å®½åº¦ä»¥åç§»å¨è·ç¦»å½±åï¼æ¯ä¸å®çå¼ï¼å æ­¤åªè½åå©ä»¥ä¸è®¡ç®æ¹å¼ã
          if (i < this.changeCount) {
            this.translateX = (this.dateList.length - this.visibleDay) * -50;
          } else {
            this.translateX = this.translateX - 50 * i;
          }
        }
      }

      /**
       * è®¡ç®å½åæ¾ç¤ºçç¬¬ä¸å¤© çåå
       * å ä¸ºå½åfunctionæ§è¡ä¸­å­å¨å¼æ­¥è¿ç¨ï¼ä¸å¨ç»æ§è¡ä¹éè¦æ¶é´ï¼å æ­¤å»¶æ¶æ§è¡
       */

      setTimeout(() => {
        // æ°æ®åæ´åçé¨åæ°æ®
        let afterChangeX = this.translateX;
        let afterChangeLen = this.dateList.length;
        // å¦æxååé½ç¸ç­ï¼åè®¡ç®å½åæ¥æåè¡¨çé¿åº¦åå
        if (beforeChangeX === afterChangeX) {
          let changeDay = afterChangeLen - beforeChangeLen;
          // å¦æç¹å»ä¹åæ¥åæ²¡åå(å½æå¤§ææå°å¼å­å¨çæ¶åï¼å°±å¯è½åºç°æ­¤ç§æåµ)ï¼åä¸å¾ä¸æ§è¡
          if (changeDay === 0) return;
          this.firstDay = this.formatOneDay(
            this.formatDateTime(
              this.firstDay.timestamp - 3600 * 1000 * 24 * changeDay
            )
          );
        } else {
          let changeDay = (afterChangeX - beforeChangeX) / 50;
          this.firstDay = this.formatOneDay(
            this.formatDateTime(
              this.firstDay.timestamp - 3600 * 1000 * 24 * changeDay
            )
          );
        }
        // äºä»¶åè°: å½åæ¾ç¤ºçç¬¬ä¸å¤©çæ°æ®
        this.$emit("firstDayChange", this.firstDay);
      }, 300);

      // äºä»¶åè°ï¼è¿åæ»å¨äºä»¶ç±»å
      this.$emit("swipeClick", { type: type });
    },
    // æ ¼å¼ååä¸ªæ¥æçæ°æ®
    formatOneDay(day) {
      let timestamp = new Date(day).getTime();
      let date = this.formatDateTime(timestamp); // 2019/06/01
      let dateArray = date.split("/"); // [2019,06,01]
      // å»æè¡¥ä½ç0
      for (const key in dateArray) {
        if (dateArray[key].indexOf("0") == 0) {
          dateArray[key] = dateArray[key].substr(1, 1);
        }
      }
      return {
        dateFormat: date,
        year: dateArray[0],
        month: dateArray[1],
        date: dateArray[2],
        timestamp: new Date(date).getTime(),
        day: this.getWeekName(new Date(timestamp).getDay())
      };
    },
    // è¿åâææâçæå­
    getWeekName(day) {
      const dirt = {
        0: this.sundayText,
        1: "ä¸",
        2: "äº",
        3: "ä¸",
        4: "å",
        5: "äº",
        6: "å­"
      };
      const dirt_en = {
        0: "Su",
        1: "Mo",
        2: "Tu",
        3: "We",
        4: "Th",
        5: "Fr",
        6: "Sa"
      };
      // å¦ææ¯è±ææ¾ç¤º
      if (this.lang === "en") {
        return dirt_en[day];
      }
      return dirt[day];
    },
    // è¾å¥æ¶é´æ³ï¼è¿å YY/MM/DD æ¥ææ ¼å¼
    formatDateTime(timestamp) {
      if (!timestamp) return "";
      timestamp = parseInt(timestamp); // é²æ­¢ä¼ å¥å­ç¬¦ä¸²ç±»å
      let fdt = new Date(timestamp);
      let arr = [fdt.getMonth() + 1, fdt.getDate()];
      for (let key in arr) {
        if (arr[key] < 10) {
          arr[key] = "0" + arr[key];
        }
      }
      arr.unshift(fdt.getFullYear());
      return arr[0] + "/" + arr[1] + "/" + arr[2];
    }
  },
  computed: {
    // æå°æ¥æç0ç¹æ¶é´æ³
    minDateTimestamp() {
      if (this.minDate) {
        let day = this.formatOneDay(this.minDate);
        return day.timestamp;
      }
      return null;
    },
    // æå¤§æ¥æç0ç¹æ¶é´æ³
    maxDateTimestamp() {
      if (this.maxDate) {
        let day = this.formatOneDay(this.maxDate);
        return day.timestamp;
      }
      return null;
    }
  }
};
</script>
